/* Ocultar las flechas en campos de entrada numérica */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Ocultar las flechas en campos de entrada numérica  para navegadores como Firefox*/
input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
  appearance: textfield; /* Otros navegadores */
}



/* contenedor categorias */
.contenedorCategorias {
  display: flex;
  flex-direction: column;
  flex: 30%;
  margin-top: 2%;
  font-family: 'Lexend', sans-serif;
  align-items: center;
}

.textNewDesign {
  text-align: center;
  margin-bottom: 20px;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(1);
  gap: 20px;
  justify-items: center;
}

.grid-item {
  text-align: center;
  padding: 20px;
  position: relative;
  width: 300px;
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  height: 110px;
  max-height: 100px;
}

.overlay {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 1.5rem;
  transition: background-color 0.3s ease;
}

.overlay:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Estilo para las imágenes de las categorías */
.imgcat {
  background: center/cover no-repeat;
}

/* Scrollbar styles for p-scrollPanel */
.p-scrollpanel .p-scrollpanel-wrapper ::-webkit-scrollbar {
  width: 12px; /* Ancho del scrollbar */
}

.p-scrollpanel .p-scrollpanel-wrapper ::-webkit-scrollbar-track {
  background: #f1f1f1; /* Color del fondo del track del scrollbar */
}

.p-scrollpanel .p-scrollpanel-wrapper ::-webkit-scrollbar-thumb {
  background: #888; /* Color del pulgar del scrollbar */
  border-radius: 10px; /* Bordes redondeados para el pulgar */
}

.p-scrollpanel .p-scrollpanel-wrapper ::-webkit-scrollbar-thumb:hover {
  background: #555; /* Color del pulgar del scrollbar al pasar el cursor */
}

/* Estilo para el scrollbar en Firefox */
.p-scrollpanel .p-scrollpanel-wrapper {
  scrollbar-width: thin; /* Ancho del scrollbar */
  scrollbar-color: #888 #f1f1f1; /* Color del pulgar y del track */
}

.p-scrollpanel .p-scrollpanel-wrapper:hover {
  scrollbar-color: #555 #f1f1f1; /* Color del pulgar al pasar el cursor */
}


