.custom-img-size {
  max-width: 100%; /* Establece el ancho máximo de la imagen al ancho del contenedor */
  height: 100%; /* Permite que la altura se ajuste automáticamente según la proporción original de la imagen */
  transition: transform 0.3s ease-in-out, filter 0.3s ease-in-out;
}

.zoom-shadow:hover .custom-img-size {
  transform: scale(1.05); /* Zoom in */
  filter: brightness(0.7); /* Sombra oscureciendo la imagen */
}

.zoom-shadow:hover .add-to-cart-button {
  opacity: 1;
}

.add-to-cart-button {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: #f3f1f1;
  color: #515151;
  border: none;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
